            <li>ak kupujúci reklamáciu vady uplatnil po dvanástich mesiacoch od kúpy a predávajúci ju zamietol, poverený zamestnanec alebo vedúci zamestnanec prevádzky, ktorý reklamáciu vybavil, je povinný v doklade o vybavení reklamácie uviesť údaj, komu môže kupujúci zaslať predmetný tovar na odborné posúdenie. Ak kupujúci odborným posúdením preukáže zodpovednosť predávajúceho za vadu, môže reklamáciu uplatniť znova; počas vykonávania odborného posúdenia záručná doba neplynie. Predávajúci je povinný kupujúcemu uhradiť do 14 dní odo dňa znova uplatnenia reklamácie všetky náklady vynaložené na odborné posúdenie, ako aj všetky s tým súvisiace účelne vynaložené náklady. Znova uplatnenú reklamáciu nemožno zamietnuť.</li></ol></li>
            <li>V prípade oprávnenosti reklamácie a splnenia reklamačných podmienok predávajúci tovar vymení za nový alebo opraví vady tovaru. Ak tak predávajúci nemôže vykonať z dôvodu nedostupnosti tovaru, vráti kupujúcemu peniaze.</li>
