            <li>Ak sa na tovare opätovne vyskytla tá istá vada po oprave, alebo väčší počet vád súčasne má kupujúci počas trvania záručnej doby právo na výmenu tohto tovaru alebo vrátenie peňazí. Opätovný výskyt tých istých vád po oprave znamená stav, keď sa rovnaká vada vyskytne po jej aspoň dvoch predchádzajúcich opravách. Za väčší počet vád tovaru sa považujú súčasne najmenej tri rôzne odstrániteľné vady, pričom každá z nich bráni v jeho riadnom užívaní.</li>
